import React ,{useEffect} from "react";
import $ from 'jquery';

function TeamCreator (props) {

  useEffect(()=> {
$('.team__container').on('touchstart mouseenter' , function (event) {
  var target = `${event.currentTarget.id} .team_info`;
  $('#'+target).css({transform : 'scale(100%)'});
});

  $(document).on('touchstart',function(event){
    if (!$(event.target).closest('.team__container').length) {
        $('.team_info').css({transform : 'scale(0)'});
      }
  });

  $('.team__container').on('mouseleave' , function () {
    $('.team_info').css({transform : 'scale(0)'});
  });




},[]);


    return (
      <div className="team__container" id={props.id}>
        <div className='team_info'>
          <h3 className='team_name'>{props.name}</h3>
          <h6 className='team_job'>{props.job}</h6>
          <ul>
            <li ><a rel="noreferrer" target='_blank' className='team_socialLink' href={props.facebook}><i className={props.facebookLogo}></i></a></li>
            <li ><a rel="noreferrer" target='_blank' className='team_socialLink' href={props.instagram}><i className={props.instagramLogo}></i></a></li>
            <li ><a rel="noreferrer" target='_blank' className='team_socialLink' href={props.behance}><i className={props.behanceLogo}></i></a></li>
          </ul>
        </div>
        <img src={props.img} alt='team' className='team__img'/>
      </div>
    );

}

export default TeamCreator;
