import React ,{useEffect} from "react";
import $ from 'jquery';

function ProjectsCreator(props) {

  useEffect(()=> {
  $('.projects__container').on('touchstart mouseenter' , function (event) {
    var target = `${event.currentTarget.id} .projects__infoAndLink`;
  $('#'+target).css({transform : 'translate(-50%, -50%) scale(100%)'});

});

  $(document).on('touchstart',function(event){
    if (!$(event.target).closest('.projects__container').length) {
        $('.projects__infoAndLink').css({transform : 'translate(-50%, -50%) scale(0)'});
      }
    });


  $('.projects__container').on('mouseleave' , function () {
    $('.projects__infoAndLink').css({transform : 'translate(-50%, -50%) scale(0)'});
});


  },[]);

  return (
    <div className="projects__container" id={props.id}>
      <div className="projects__infoAndLink">
        <h1>{props.info}</h1>
        <a className='btn' href={props.link} rel="noreferrer" target='_blank'>Visit</a>
      </div>
      <img src={props.img} alt="project" />
    </div>
  );
}

export default ProjectsCreator;
